import React, {Component} from "react";
import {PropTypes} from "prop-types";
import {OraModal} from "eshop/components/OraCommonComponents";
import {ResponsiveVisibility} from "eshop/modules/core/components/ui/RWDUtils";
import {DesktopPriceBox, MobilePriceBox} from "eshop/modules/cart/components/entriesList/shared/PriceBoxes";
import OnlineUtils from "eshop/utils/OnlineUtils";

class VASDetailsModal extends Component  {
    constructor (props) {
        super(props);
    }

    renderSimple(){
        var detailsHTML = {__html: this.props.details};
        return (this.props.details &&
            <div dangerouslySetInnerHTML={detailsHTML}></div>
        );
    }

    render(){
        var details = this.renderSimple();
        return(
        <OraModal id={this.props.id} showClose={true} narrow={true}>
            <div>
                <VASDetailsHeader icon={this.props.icon} header={this.props.header} productName={this.props.productName} slogan={this.props.slogan} prices={this.props.prices}/>
                <div className="l-full-row">
                    <div className="l-col l-col-10 l-col-medium-12 l-col-small-12 large-offset-by-2 u-small-margin-top u-margin">
                        <div className="o-separator"></div>
                    </div>
                </div>
                <div className="l-full-row">
                    <div className="l-col l-col-10 l-col-medium-12 l-col-small-12 large-offset-by-2">
                        {details}
                    </div>
                </div>
            </div>
        </OraModal>)
    }
}

VASDetailsModal.defaultProps = {
    id: "",
    details: "",
    slogan: "",
    header : "default: Szczegóły usługi"
}

VASDetailsModal.propTypes = {
     id: PropTypes.string,
     icon: PropTypes.string,
     details: PropTypes.string,
     slogan: PropTypes.string,
     productName: PropTypes.string,
     header: PropTypes.string,
     prices: PropTypes.arrayOf(PropTypes.object)
}

const VASDetailsHeader = (props) => {
    var icon = props.icon ? props.icon : "funpack-2";
    var iconSize = " g-icon--l";
    var prices = props.prices.map((charge) => {
        let priceStr = OnlineUtils.formatPrice(charge.price)
        return ({
            integer: priceStr.split(',')[0],
            fraction: priceStr.split(',')[1],
            currency: charge.currency,
            start: charge.monthFrom,
            end: charge.monthTo
        });
    });

    return(
        <div>
            <p className="h3 u-spacing-l">{props.header}</p>
            <VASDetailsDesktopHeader icon={icon} iconSize={iconSize} productName={props.productName} slogan={props.slogan} prices={prices}/>
            <VASDetailsMobileHeader icon={icon} iconSize={iconSize} productName={props.productName} slogan={props.slogan} prices={prices}/>
        </div>
    );
}

const VASDetailsDesktopHeader = (props) => {
    return (<ResponsiveVisibility mobile={false}>
        <div className="l-full-row">
            <div className="l-col l-col-2">
                <span className={"u-padding-top-l u-padding-m u-large-left u-medium-left g-icon g-icon--only g-icon--"+props.icon+props.iconSize}></span>
            </div>
            <div className="l-col l-col-6">
                <p className="h5 u-spacing-top-l">{props.productName}</p>
                {props.slogan && <p className="u-margin" dangerouslySetInnerHTML={{__html: props.slogan}}></p>}
            </div>
            <div className="l-col l-col-4">
                <DesktopPriceBox prices={props.prices}/>
            </div>
        </div>
    </ResponsiveVisibility>);
}


const VASDetailsMobileHeader = (props) => {
    return (<ResponsiveVisibility desktop={false} mobile={true}>
        <div className="l-full-row">
            <div className="l-col l-col-5">
                <span className={"g-icon g-icon--only g-icon--"+props.icon+props.iconSize}></span>
            </div>
            <div className="l-col l-col-7">
                <MobilePriceBox prices={props.prices}/>
            </div>
            <div className="l-col l-col-12">
                <p className="h5 u-spacing-top-m">{props.productName}</p>
                {props.slogan && <p className="u-margin" dangerouslySetInnerHTML={{__html: props.slogan}}></p>}
            </div>
        </div>
    </ResponsiveVisibility>);
}

export default VASDetailsModal;